import { Pipe, PipeTransform } from '@angular/core';
import { OutsideExamType } from '../enums/quiz.enum';

@Pipe({
  name: 'outsideExamTypePipe'
})
export class OutsideExamTypePipe implements PipeTransform {

  transform(type: unknown): unknown {
    switch (type) {
      case OutsideExamType.foreignCurrency:
        return '';
      case OutsideExamType.invest:
        return '';
      case OutsideExamType.professional:
      case OutsideExamType.common:
        return '(/)';
      default:
        return '-';
    }
  }

}
