import { Component, Input, OnChanges, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { errorMsg, MessageService } from 'merc-lib';
import { NzModalRef, NzModalService } from 'ng-zorro-antd/modal';
import { ICON_PATH } from '../../../const/icon-path.const';

@Component({
  selector: 'app-change-place',
  templateUrl: './change-place.component.html',
  styleUrls: ['./change-place.component.scss']
})
export class ChangePlaceComponent implements OnInit, OnChanges {
  @Input() quiz!: any;
  get ICON_PATH() {
    return ICON_PATH;
  }
  validateForm!: FormGroup;
  errorMsg = errorMsg;
  constructor(
    public modal: NzModalRef,
    private fb: FormBuilder,
    private modalService: NzModalService,
    private messageService: MessageService
  ) { }

  ngOnInit(): void {
    console.log('???');
    console.log(this.quiz);
    this.initForm();

  }
  ngOnChanges() {
    console.log(this.quiz);
  }
  private initForm() {
    console.log(this.quiz.location === '');

    this.validateForm = this.fb.group({
      name: [{ value: this.quiz.title, disabled: true }],
      location: [this.quiz.location, Validators.required],
    });
    console.log(this.validateForm.value);

  }
  onSendClick(): void {
    // TODO: 
    const originLocate = '';
    const locate = '';
    for (const i in this.validateForm.controls) {
      if (i) {
        this.validateForm.controls[i].markAsDirty();
        this.validateForm.controls[i].updateValueAndValidity();
      }
    }
    if (this.validateForm.valid) {
      const desc = `<div><span class="text-secondary">${originLocate}</span><span class="text-secondary">${locate}</span></div><div></div>`;
      this.messageService.successModal(desc, {imgPath: './assets/image/icon/icon_success@3x.png', msgTitle: '', isHtmlCode: true });
      this.modal.triggerOk();
    }
  }
}
